import React from "react"
// Libs
import getPageData from "globalLibs/helpers/get-page-data"
// Atoms
import Seo from "globalAtoms/Seo"
// Organisms
import Layout from "globalOrganisms/Layout"
import Sidebar from "projectOrganisms/Sidebar"
import MainNav from "projectOrganisms/MainNav"

export default function Contacts(props) {
  const pageData = getPageData(props)

  return (
    <Layout className="flex-col">
      <Seo pageData={pageData} />
      <Sidebar />
      <MainNav />
      <article className="container mx-auto max-w-screen-lg mt-12">
        <h1 className="mb-0">{pageData.title}</h1>
        <hr />
        <ul className="list-none">
          <li>
            Email - <a href="mailto:w@res.pm">w@res.pm</a>
          </li>
          <li>
            Telegram -{" "}
            <a href="https://t.me/Attvud" target="blank">
              t.me/Attvud
            </a>
          </li>
          <li>
            GitHub -{" "}
            <a href="https://github.com/Attvud" target="blank">
              github.com/Attvud
            </a>
          </li>
          <li>
            Linkedin -{" "}
            <a href="https://www.linkedin.com/in/Attvud" target="blank">
              linkedin.com/in/Attvud
            </a>
          </li>
        </ul>
      </article>
    </Layout>
  )
}
