import React from "react"
// Atoms
import Icon from "globalAtoms/Icon"
import ThemeSwitcher from "globalAtoms/ThemeSwitcher"

export default function MainNav(props) {
  return (
    <div className="flex justify-center h-12 relative select-none">
      <div
        className="flex justify-center text-hight-contrast w-full fixed z-20 bg-hight-contrast-reverse"
        style={{
          boxShadow: "inset 0px -1px 0px var(--separator-non-opaque)",
          backdropFilter: "saturate(180%) blur(16px)",
          backgroundColor: "rgb(var(--background-primary-rgb-inner)/90%)",
        }}
      >
        <nav
          className={`container flex items-center max-w-screen-lg h-12 ${props.className}`}
        >
          <div className="flex justify-center md:justify-between h-12 items-center w-full large-subhead">
            <a
              href="/"
              className="flex h-full items-center whitespace-nowrap transition text-hight-contrast opacity-80 hover:opacity-100 hover:text-hight-contrast"
            >
              <svg
                width="24"
                height="24"
                viewBox="0 0 24 24"
                xmlns="http://www.w3.org/2000/svg"
                className="fill-current text-hight-contrast"
              >
                <path d="M13.3603 2L20.7919 14.8719L16.939 22L13.6065 16.2281L15.4747 12.7715L11.7748 13.0555L9.50735 9.12808L13.3603 2ZM11.9624 19.2519L7.98122 11.953L4 19.2519H11.9624Z" />
              </svg>
              <span className="ml-3">Artem Attvud</span>
            </a>
            <a
              href="/resume"
              className="hidden md:flex h-full flex items-center transition text-hight-contrast opacity-80 hover:opacity-100 hover:text-hight-contrast"
            >
              Resume
            </a>
            <a
              href="https://www.linkedin.com/in/attvud"
              target="blank"
              className="hidden md:flex h-full flex items-center transition text-hight-contrast opacity-80 hover:opacity-100 hover:text-hight-contrast"
            >
              Recommendations
            </a>
            <a
              href="/portfolio"
              className="hidden md:flex h-full flex items-center transition text-hight-contrast opacity-80 hover:opacity-100 hover:text-hight-contrast"
            >
              Portfolio
            </a>
            <a
              href="/contacts"
              className="hidden md:flex h-full items-center transition text-hight-contrast opacity-80 hover:opacity-100 hover:text-hight-contrast"
            >
              Contacts
            </a>
            <div className="hidden md:flex transition opacity-80 hover:opacity-100">
              <ThemeSwitcher className="fill-current text-hight-contrast" />
            </div>
          </div>

          {/* <div className="flex h-12 items-center">
            <Icon icon="print" onClick={() => window.print()} />
          </div> */}
        </nav>
      </div>
    </div>
  )
}
