import styled from "styled-components"

export const ElementSection = styled.div`
  margin-top: 24px;
  & .section-title {
    color: var(--gray-01);
    text-transform: uppercase;
  }
  & .section-caption {
    color: var(--gray-01);
  }
`

export const ElementList = styled.div`
  background-color: var(--background-primary);
  & a {
    color: var(--text-primary);
    &:hover {
      color: var(--text-primary);
    }
  }
  div:not(:last-child):after {
    position: absolute;
    content: "";
    display: flex;
    width: 100%;
    height: 1px;
    background-color: var(--separator-non-opaque);
    bottom: 0;
    margin-left: 32px;
    cursor: pointer;
  }
`
