import React, { useState, useEffect } from "react"
import HeaderContentLayerDefault from "projectOrganisms/Sidebar/content-layers/header/default"
import HeaderContentLayerMenu from "projectOrganisms/Sidebar/content-layers/header/menu"
import MainContentLayerDefault from "projectOrganisms/Sidebar/content-layers/main/default"
import MainContentLayerMenu from "projectOrganisms/Sidebar/content-layers/main/menu"
import Icon from "globalAtoms/Icon"

export default function Sidebar() {
  const [headerContentLayer, setHeaderContentLayer] = useState("default")
  const [mainContentLayer, setMainContentLayer] = useState("default")
  const [footerContentLayer, setFooterContentLayer] = useState("default")

  let sidebar, sidebarClassList

  useEffect(() => {
    sidebar = document.getElementById("main-sidebar-content")
  })

  function sidebarVisibilityHandler(neededContentLayer) {
    if (mainContentLayer !== neededContentLayer) {
      setHeaderContentLayer(neededContentLayer)
      setMainContentLayer(neededContentLayer)
      setFooterContentLayer(neededContentLayer)
    }
    if (
      mainContentLayer === neededContentLayer ||
      sidebar.classList.contains("invisible") ||
      sidebar.classList.contains("visible")
    ) {
      sidebar.classList.toggle("invisible")
      sidebar.classList.toggle("visible")
    } else {
    }

    return
  }

  function getHeaderContentLayer(headerContentLayer) {
    switch (headerContentLayer) {
      case "menu":
        return <HeaderContentLayerMenu />
      default:
        return <HeaderContentLayerDefault />
    }
  }

  function getMainContentLayer(mainContentLayer) {
    switch (mainContentLayer) {
      case "menu":
        return <MainContentLayerMenu />
      default:
        return <MainContentLayerDefault />
    }
  }

  function getFooterContentLayer(footerContentLayer) {
    switch (footerContentLayer) {
      // case "default":
      //   return <FooterContentLayerDefault />
      default:
        return (
          <>
            <a className="flex flex-col justify-center items-center" href="/">
              <Icon icon="home" className="fill-current text-hight-contrast" />
              <span className="large-caption text-hight-contrast">Home</span>
            </a>
            <a
              href="/contacts"
              className="flex flex-col justify-center items-center"
            >
              <Icon
                icon="contact_support"
                className="fill-current text-hight-contrast"
              />
              <span className="large-caption text-hight-contrast">
                Contacts
              </span>
            </a>
            <button
              className="flex flex-col justify-center items-center focus:outline-none"
              onClick={event => {
                const neededContentLayer = "menu"
                sidebarVisibilityHandler(neededContentLayer)
              }}
            >
              <Icon
                icon="more_horiz"
                className="fill-current text-hight-contrast"
              />
              <span className="large-caption text-hight-contrast">Menu</span>
            </button>
          </>
        )
    }
  }

  return (
    <aside
      className="flex md:hidden fixed flex-col z-30"
      style={{
        maxWidth: "360px",
      }}
    >
      <div
        className="fixed bg-background-secondary w-full h-screen invisible"
        style={{
          boxShadow: "1px 0px 0px var(--separator-non-opaque)",
          maxWidth: "360px",
        }}
        id="main-sidebar-content"
      >
        <header
          className="bg-background-primary"
          style={{
            boxShadow: "inset 0px -1px 0px var(--separator-non-opaque)",
          }}
        >
          {getHeaderContentLayer(headerContentLayer)}
        </header>
        {getMainContentLayer(mainContentLayer)}
      </div>
      <footer
        className="grid grid-flow-col grid-cols-3 h-14 w-full fixed bottom-0 bg-background-primary"
        style={{
          boxShadow: "inset 0px 1px 0px var(--separator-non-opaque)",
        }}
      >
        {getFooterContentLayer(footerContentLayer)}
      </footer>
    </aside>
  )
}
