import React from "react"
import * as S from "./styles"
import Row from "globalAtoms/Row"

export default function ElementList(props) {
  const children = props.children
  const listTitle = props.title
  const listDescription = props.description

  return (
    <div className="mt-6">
      {/* {props.title !== undefined && (
        <Row className="element-small section-title">
          <span className="left-part px-16">{listTitle}</span>
        </Row>
      )} */}
      <S.ElementList
        className={props.listClassName}
        style={{
          boxShadow: `inset 0 1px 0 var(--separator-non-opaque),inset 0 -1px 0 var(--separator-non-opaque)`,
        }}
      >
        {children}
      </S.ElementList>
      {/* {props.description !== undefined && (
        <Row className="element-small section-caption justify-content-start">
          <div className="left-part position-relative py-8 px-16">
            {listDescription}
          </div>
        </Row>
      )} */}
    </div>
  )
}
